body {
  margin: 20px;
  padding: 20px;
  font-family: 'Raleway', sans-serif;
  background: #f9f9f9;
  --link_color: #ac863e;
  --header_color: #5f4a89;
  --overview_color: #241444b0;
  --bullet_line_color: #242424;
}

a {
  color: var(--link_color);
  text-decoration: none;
}

h1, h2, li {
  color: var(--header_color)
}